import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../service/auth.service';
import { NgFlashMessageService } from 'ng-flash-messages';
import { Router } from '@angular/router';


@Component({
  selector: 'app-investform',
  templateUrl: './investform.component.html',
  styleUrls: ['./investform.component.css']
})
export class InvestformComponent implements OnInit {
  invamount:number;
  invdur:number;
  invmethod:String;
  nic:String;
  acc:number;
  constructor(
    private authservice:AuthService,
    private ngFlashMessageService: NgFlashMessageService,
    private router:Router,
  ) { }

  ngOnInit() {
  }
  invformsend(){
    const invform={
      invamount:this.invamount,
      invdur:this.invdur,
      invmethod:this.invmethod,
      nic:this.nic,
      acc:this.acc
    }
    this.authservice.invformsend(invform).subscribe(res=>{
      if(res.state){
      this.ngFlashMessageService.showFlashMessage({messages: ["You are registered!"],dismissible: true,timeout: 4000,type: 'success'});
      this.router.navigate(['/login']);}
      else{
      console.log(res.msg);
      this.ngFlashMessageService.showFlashMessage({messages: ["Something went wrong!"],dismissible: false,timeout: 4000,type: 'danger'});
      this.router.navigate(['/register']);
      }
    });
}
}



