import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  myStyle: object = {};
    myParams: object = {};
    width: number = 100;
    height: number = 100;
    click:String;
  constructor() { }

  ngOnInit() {
    this.myStyle = {
      'position': 'fixed',
      'width': '100%',
      'height': '100%',
      'z-index': -1,
      'top': 0,
      'left': 0,
      'right': 0,
      'bottom': 0,
  };

  

this.myParams = {
      particles: {
          number: {
              value: 200,
          },
          color: {
              value: '#ff0000'
          },
          shape: {
              type: 'triangle',
          },
  }
};

  }

imageSources = ["../../../assets/slidshow/Slide1.JPG",
  "../../../assets/slidshow/Slide2.JPG",
  "../../../assets/slidshow/Slide3.JPG","../../../assets/slidshow/Slide4.JPG","../../../assets/slidshow/Slide5.JPG","../../../assets/slidshow/Slide4.JPG","../../../assets/slidshow/Slide6.JPG"];

  myFunc(){
    this.click=null;
    this.click="invest";
  }
  myFunc2(){
    this.click=null;
    this.click="borrow";
  }

  isinvest(){
    if(this.click=="invest"){
      return true;
    }
    
  }
  isborrow(){
    if(this.click=="borrow"){
      return true;
    }
    
  }

}



