import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../service/auth.service';
import { NgFlashMessageService } from 'ng-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-loanform',
  templateUrl: './loanform.component.html',
  styleUrls: ['./loanform.component.css']
})
export class LoanformComponent implements OnInit {
  loanamount:number;
  loandur:number;
  purpose:String;
  gender:String;
  nic:String;
  tpfixed:number;
  tpmobile:number;
  otherincome:String;

  bname:String;
  bowner:String;
  regno:String;
  type:String;
  profit:number;
  bacc:number;
  bdetails:String;
  bstart:String;

  rname:String;
  rnic:String;
  rphone:number;
  raddress:String;
  rincome:number;





  constructor(
    private authservice:AuthService,
    private ngFlashMessageService: NgFlashMessageService,
    private router:Router,
  ) { }

  ngOnInit() {
  
  }
  
  loanformsend(){
    
    const loanform={
      
      loanamount:this.loanamount,
      loandur:this.loandur,
  purpose:this.purpose,
  gender:this.gender,
  nic:this.nic,
  tpfixed:this.tpfixed,
  tpmobile:this.tpmobile,
  otherincome:this.otherincome,

  bname:this.bname,
  bowner:this.bowner,
  regno:this.regno,
  type:this.type,
  profit:this.profit,
  bacc:this.bacc,
  bdetails:this.bdetails,
  bstart:this.bstart,

  rname:this.rname,
  rnic:this.rnic,
  rphone:this.rphone,
  raddress:this.raddress,
  rincome:this.rincome
    }
    this.authservice.loanFormSend(loanform).subscribe(res=>{
      if(res.state){
      this.ngFlashMessageService.showFlashMessage({messages: ["You are registered!"],dismissible: true,timeout: 4000,type: 'success'});
      this.router.navigate(['/login']);}
      else{
      console.log(res.msg);
      this.ngFlashMessageService.showFlashMessage({messages: ["Something went wrong!"],dismissible: false,timeout: 4000,type: 'danger'});
      this.router.navigate(['/register']);
      }
    });
}
}



