import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule,Routes } from '@angular/router';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';
import { NgFlashMessagesModule } from 'ng-flash-messages';

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { ProfileComponent } from './components/profile/profile.component';
import { Router } from '@angular/router/src/router';
import { AuthService } from './service/auth.service';
import { AuthGuard } from './service/auth.guard';
import { HomeComponent } from './components/home/home.component';
import {SlideshowModule} from 'ng-simple-slideshow';
import { ParticlesModule } from 'angular-particle';
import { FooterComponent } from './components/footer/footer.component';
import { InvestComponent } from './components/invest/invest.component';
import { BorrowComponent } from './components/borrow/borrow.component';
import { LoanformComponent } from './components/loanform/loanform.component';
import { InvestformComponent } from './components/investform/investform.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';


const applicationRoutes:Routes = [
  {path:'login',component:LoginComponent},
  {path:'dashboard',component:DashboardComponent, canActivate: [AuthGuard]},
  {path:'loanform',component:LoanformComponent, canActivate: [AuthGuard]},
  {path:'investform',component:InvestformComponent, canActivate: [AuthGuard]},
  {path:'register',component:RegisterComponent},
  {path:'profile',component:ProfileComponent, canActivate: [AuthGuard]},
  {path:'',component:HomeComponent},
];

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    ProfileComponent,
    HomeComponent,
    FooterComponent,
    InvestComponent,
    BorrowComponent,
    LoanformComponent,
    InvestformComponent,
    DashboardComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    SlideshowModule,
    ParticlesModule,
    RouterModule.forRoot(applicationRoutes),
    NgFlashMessagesModule.forRoot()
  ],
  providers: [AuthService,AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule {

 }



